<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "historyAnnouncement-component",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      form: {
        bbsCat: "0", // 公告類型
        aging: "0", // 時效性
        locationId: "ALL", // 所屬廠區
      },

      bbsHistoryTableColumns: [
        { id: "index", label: "項次", width: "75" },
        { id: "title", label: "訊息標題", width: "300" },
        { id: "file_Count", label: "附加檔案數量", width: "125" },
        { id: "comments", label: "發布 部門名稱-姓名", width: "210" },
        { id: "valid_Date", label: "發布 開始-結束日期", width: "180" },
        { id: "bbs_Info", label: "訊息分類/所屬廠區", width: "180" },
      ],
    };
  },
  computed: {
    // 所屬廠區總清單
    bbsLocList() {
      return this.$store.state.bbsLoc_list;
    },
    // 歷史公告資料 (All)
    bbsHistory() {
      return this.$store.state.bbsHistory_all;
    },
    // 歷史公告資料 (單頁)
    bbsHistoryList() {
      return this.$store.state.bbsHistory_allList;
    },
  },
  methods: {
    // 查詢相應的歷史公告，呈現表格第一頁
    async qryBbsHistory() {
      try {
        await this.$store.dispatch("getBbsHistory", {
          bbsCat: this.form.bbsCat,
          aging: this.form.aging,
          locationId: this.form.locationId,
        });
        // this.$refs.form.resetFields();
        this.$store.commit("CURRENT_PAGE_INIT");
      } catch (err) {
        console.log(err);
      }
    },

    changePage(page) {
      this.$store.commit("CHANGE_PAGE_HANDLER", {
        page,
        paramsDataName: "bbsHistory_all",
        paramsListName: "bbsHistory_allList",
      });
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    try {
      // 清空先前的查詢結果
      this.$store.state.bbsHistory_all = [];
      this.$store.state.bbsHistory_allList = [];

      // 打 API 取得廠區資料
      await this.$store.dispatch("getBbsLocation");
    } catch (err) {
      console.log(err);
    }
    this.$store.commit("CURRENT_PAGE_INIT");
  },
};
</script>

<template src="./historyAnnouncement.html"></template>
